/* eslint-disable react/display-name */
import React, { Component } from 'react'
import { debounce } from '@elliemae/ds-utilities/utils'

const withSearchable = WrappedComponent => (class extends Component {
  debouncedSearch = debounce(this.handleSearch, 200)
  static defaultProps = {
    onSearch: () => null,
    onKeyUp: () => null,
    property: 'all',
    searchOnEnter: true,
    clearOnSearch: true,
    containerProps: {},
  }
  constructor(props) {
    super(props)
    this.state = {
      value: props.value,
      prevPropValue: '',
    }
    this.handleSearch = this.handleSearch.bind(this)
    this.handleKeyUp = this.handleKeyUp.bind(this)
    this.handleChange = this.handleChange.bind(this)
    this.buttonRef = React.createRef()
  }
  static getDerivedStateFromProps(nextProps, { prevPropValue }) {
    if (nextProps.value !== prevPropValue) {
      return {
        value: nextProps.value,
        prevPropValue: nextProps.value,
      }
    }
    return null
  }
  handleKeyUp(e) {
    const { onKeyUp, searchOnEnter } = this.props
    const { key } = e
    if (searchOnEnter && key === 'Enter') {
      this.handleSearch()
    }
    onKeyUp(e)
  }
  handleSearch() {
    const {
      onSearch,
      property,
      clearOnSearch,
      searchOnEnter,
    } = this.props
    const { value } = this.state

    if (clearOnSearch) this.setState({ value: '' })
    if (searchOnEnter && !value) return
    // if (this.buttonRef.current) this.buttonRef.current.focus()
    onSearch({
      query: { [property]: value },
      property,
      value,
    })
  }
  handleChange({ target }) {
    const { value } = target
    const { searchOnEnter } = this.props

    this.setState({ value }, () => {
      if (!searchOnEnter) this.debouncedSearch()
    })
  }
  render() {
    const { value } = this.state
    const {
      containerProps,
      ...rest
    } = this.props

    return (
      <WrappedComponent
        {...rest}
        buttonRef={this.buttonRef}
        containerProps={containerProps}
        onChange={this.handleChange}
        onKeyUp={this.handleKeyUp}
        onSearch={this.handleSearch}
        value={value}
      />)
  }
})

export default withSearchable
