/* eslint-disable react/display-name,react/destructuring-assignment */
import React, { useContext, useRef } from 'react';
import { runAll } from '@elliemae/ds-utilities/utils';
import { ToolbarContext } from '@elliemae/ds-shared/toolbar/ToolbarProvider';

// todo: prevent rerendering caused by react-context
// https://github.com/facebook/react/issues/15156
// https://github.com/facebook/react/issues/14110
export function RowToolbarHoc(Component, grid, rowData = {}) {
  return props => {
    const { onMouseEnter, onMouseLeave, isScrolling = false, ...rest } = props;

    const {
      props: { renderToolbar, toolbarDelayClose, rowSize },
    } = grid.getInstance();

    if (isScrolling) {
      return <Component isScrolling={isScrolling} {...rest} />;
    }

    const toolbarContext = useContext(ToolbarContext);
    const ref = useRef();

    const show = () => {
      if (ref.current) {
        const toolbar = renderToolbar({
          onCloseToolbar: toolbarContext.hide,
          ...rowData,
          size: rowSize,
        });

        toolbarContext.show({
          reference: ref.current,
          renderer: toolbar,
          // https://jira.elliemae.io/browse/PUI-1663
          style: { zIndex: 30 },
          delayClose: toolbarDelayClose, // https://jira.elliemae.io/browse/PUI-1731
        });
      }
    };

    // the hide action should be handled by the tooltip
    // const hide = () => {
    //   toolbarContext.hide()
    // }

    return (
      <Component
        {...rest}
        ref={ref}
        aria-expanded={
          toolbarContext.state.reference === ref.current &&
          toolbarContext.state.visible
        }
        onClick={runAll(show, props.onClick)}
        onMouseEnter={runAll(show, props.onMouseEnter)}
        // the hide action should be handled by the tooltip
        onMouseLeave={runAll(props.onMouseLeave)}
      />
    );
  };
}

export default RowToolbarHoc;
