import React from 'react'
import { Menu } from '@elliemae/ds-basic/Menu'

const ColumnsOptionsMenuSection = ({
  columns,
  onColumnVisibilityChange,
  searchable = false,
  height,
// eslint-disable-next-line react/display-name
}) => () => {
  const { items, active } = columns.reduce((result, column, index) => ({
    items: [
      ...result.items, {
        id: column.property,
        label: column.label,
        checked: column.visible,
        onClick: () => onColumnVisibilityChange(column, index),
      }],
    active: typeof column.visible === 'undefined' || column.visible
      ? [...result.active, column.property]
      : result.active,

  }), { items: [], active: [] })
  return (
    <Menu.SelectionGroup
      active={active}
      height={height}
      items={items}
      multi
      searchable={searchable}
    />
  )
}

export default ColumnsOptionsMenuSection
