import React from 'react'
import PropTypes from 'prop-types'
import { aggregatedClasses } from '@elliemae/ds-classnames'

const pillBlockName = 'pill'

const PillWrapper = aggregatedClasses('div')(pillBlockName, null, ({ variant }) => ({ [variant]: !!variant }))
const PillLabel = aggregatedClasses('span')(pillBlockName, 'label')
const PillAddon = aggregatedClasses('div')(pillBlockName, 'addon')

export function Pill({
  containerProps = {},
  label = '',
  leftAddon,
  rightAddon,
  variant = 'value',
  ...otherProps
}) {
  return (
    <PillWrapper
      {...containerProps}
      classProps={{ variant }}
      {...otherProps}
    >
      {leftAddon && <PillAddon>{leftAddon}</PillAddon>}
      <PillLabel>{label}</PillLabel>
      {rightAddon && <PillAddon>{rightAddon}</PillAddon>}
    </PillWrapper>
  )
}

Pill.propTypes = {
  containerProps: PropTypes.shape({}),
  label: PropTypes.string.isRequired,
  variant: PropTypes.oneOf(['value', 'title']),
}
