import React, {
  useRef,
  useState,
  useEffect,
  useContext,
} from 'react'
import { TruncatedTooltipContext } from "@elliemae/ds-basic/TruncatedTooltipText"
import useFocusGroupItem from '@elliemae/ds-shared/FocusGroup/useFocusGroupItem'
import { Pill } from './Pill'

export function OverflowPill({
  containerProps = {},
  label = '',
  options = [],
}) {
  const ref = useRef(null)
  const [tooltipText, setTooltipText] = useState('')

  useEffect(() => {
    let text = ''
    options.forEach((option) => {
      if (!tooltipText || option === options[0]) {
        text = `${option.label}`
      } else {
        text = text.concat(`, ${option.label}`)
      }
    })
    setTooltipText(text)
  }, [options])

  const tooltipContext = useContext(TruncatedTooltipContext)

  if (!tooltipContext) return tooltipText

  const { showTooltip, hideTooltip } = tooltipContext

  const handleMouseEnter = (e) => {
    const { target } = e
    showTooltip({
      value: tooltipText, reference: target,
    }, e)
  }

  const handleMouseLeave = (e) => {
    hideTooltip({ reference: e.target })
  }

  const handlers = showTooltip
    ? {
      onMouseEnter: handleMouseEnter,
      onMouseLeave: handleMouseLeave,
    }
    : {}

  useFocusGroupItem(ref)

  return (
    <Pill
      className='overflow-pill'
      containerProps={containerProps}
      innerRef={ref}
      label={label}
      tabIndex={-1}
      {...handlers}
    >
      {tooltipText}
    </Pill>
  )
}
