/* eslint-disable max-statements */
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { isEmpty } from 'lodash';
import { ToolbarTrigger } from './ToolbarTrigger';
import { RowSizes } from '../../rowSizes';

export const RowRenderer = (Row, grid) => {
  return props => {
    const ref = useRef();
    const requestAnimationRef = useRef();
    const { rowData = {}, index, rowIndex, style } = props;
    const {
      props: { wrapText },
    } = grid.getInstance();
    const Trigger = ToolbarTrigger(grid, { rowData, index, rowIndex });
    const [bodyActualSize, setBodyActualSize] = useState(0);
    const [bodyScrollLeft, setScroll] = useState(0);
    const [popStyle, setPopStyle] = useState({});
    const [rowStyle, setRowStyle] = useState(style);
    const [buttonMarginTop, setButtonMarginTop] = useState(0);
    const [triggerStyle, setTriggerStyle] = useState({});
    const gridInstance = grid.getInstance();
    const { refs } = gridInstance;
    const [hasVerticalScrollBar, setHasVerticalScrollBar] = useState(
      refs.body.current &&
        refs.body.current.scrollHeight > refs.body.current.clientHeight,
    );

    const getScroll = () => {
      const innerBodyScroll =
        refs.innerBody.current && refs.innerBody.current.scrollLeft;
      return (
        (innerBodyScroll || refs.body.current.scrollLeft) -
        (hasVerticalScrollBar ? 0 : 16)
      );
    };

    const scrollUpdate = () => {
      setBodyActualSize(refs.body.current.offsetWidth);
      const bodyScroll = getScroll(refs);
      setScroll(bodyScroll);
      requestAnimationRef.current = requestAnimationFrame(scrollUpdate);
    };

    useEffect(() => {
      scrollUpdate();
      return () => {
        cancelAnimationFrame(requestAnimationRef.current);
      };
    }, []);

    const generateStyles = useCallback(
      styleScroll => {
        return {
          zIndex: 2,
          transform: style ? null : `translateX(${styleScroll}px)`,
          top: style ? style.top : 'auto',
          position: style ? 'absolute' : 'relative',
          left: style ? bodyActualSize + styleScroll : 'auto',
          transformStyle: 'preserve-3d',
          background: 'white',
        };
      },
      [style, bodyActualSize],
    );

    useEffect(() => {
      setPopStyle({
        ...popStyle,
        ...generateStyles(bodyScrollLeft),
      });
      if (style) {
        // style defined means it has virtualization
        setHasVerticalScrollBar(true);
      } else {
        setHasVerticalScrollBar(
          refs.body.current.scrollHeight > refs.body.current.clientHeight,
        );
      }
      setRowStyle(
        style || {
          height: !wrapText ? RowSizes[gridInstance.props.rowSize] : 'auto',
          position: 'relative',
        },
      );
    }, [style, bodyScrollLeft, bodyActualSize]);

    useEffect(() => {
      const rowSize = gridInstance.props.rowSize;
      const triggerStyle = { position: 'absolute', top: '50%', width: '100%' };
      if (style) {
        setButtonMarginTop(4);
        setTriggerStyle({});
      } else if (rowSize === 'normal') {
        setButtonMarginTop(-14);
        setTriggerStyle(triggerStyle);
      } else if (rowSize === 'compact') {
        setButtonMarginTop(-12);
        setTriggerStyle(triggerStyle);
      }
    }, [style, gridInstance.props.rowSize]);

    return (
      <div
        className="row-renderer-fixed-height"
        style={{
          height: !wrapText ? RowSizes[gridInstance.props.rowSize] : 'auto',
          position: style ? 'auto' : 'relative',
        }}
      >
        <Row ref={ref} {...props} style={rowStyle} />
        {isEmpty(rowData) && !rowData.index && !rowData.rowIndex ? null : (
          <div style={{ ...popStyle, ...triggerStyle }}>
            <Trigger
              buttonMarginTop={buttonMarginTop}
              hasScrollBar={hasVerticalScrollBar}
            />
          </div>
        )}
      </div>
    );
  };
};
