/* eslint-disable react/display-name */
import React, { useRef } from 'react';
import ArrowheadDown from '@elliemae/ds-icons/ArrowheadDown';
import ArrowheadRight from '@elliemae/ds-icons/ArrowheadRight';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import DSButton from '@elliemae/ds-basic/Button';
import DSTruncatedTooltipText from '@elliemae/ds-basic/TruncatedTooltipText';
import { dataGridBlockName } from '../../blockNames';
import { RowSizes } from '../../rowSizes';

const blockName = `${dataGridBlockName}-expandable-column`;

// todo: clean this when we move to css-js
const ExpandableColumnWrapper = aggregatedClasses('div')(blockName);
const LabelDescriptionWrapper = aggregatedClasses('div')(
  blockName,
  'label-description-wrapper',
);
const Label = aggregatedClasses('span')(blockName, 'label');
const Description = aggregatedClasses('span')(blockName, 'description');
const ExtraColumnData = aggregatedClasses('div')(
  blockName,
  'extra-column-data',
);
const ExpandableMasterWrapper = aggregatedClasses('div')(
  `${blockName}-master-wrapper`,
);
const ExpandableDetailsWrapper = aggregatedClasses('div')(
  `${blockName}-details-wrapper`,
);

function ExpandableColumnCell({
  label,
  description,
  isExpanded,
  expandableDetails,
  extraData,
  onToggleExpand,
}) {
  const ref = useRef();
  return (
    <ExpandableColumnWrapper ref={ref} tabindex={0}>
      <ExpandableMasterWrapper style={{ height: RowSizes.extend }}>
        <DSButton
          buttonType="text"
          className="expandable-arrow"
          icon={isExpanded ? <ArrowheadDown /> : <ArrowheadRight />}
          onClick={e => {
            onToggleExpand(e);
            if (ref && ref.current) {
              ref.current.focus();
            }
          }}
          size="s"
        />
        <LabelDescriptionWrapper>
          <Label>
            <DSTruncatedTooltipText value={label} />
          </Label>
          <Description>
            <DSTruncatedTooltipText value={description} />
          </Description>
        </LabelDescriptionWrapper>
        <ExtraColumnData>{extraData}</ExtraColumnData>
      </ExpandableMasterWrapper>
      {isExpanded && (
        <ExpandableDetailsWrapper>{expandableDetails}</ExpandableDetailsWrapper>
      )}
    </ExpandableColumnWrapper>
  );
}

const expandableColumnRenderer = ({
  renderRowLabel,
  renderRowDescription,
  renderExtraData,
}) => (value, { rowData, isExpanded }, grid) => {
  const {
    props: { renderExpandedDetails },
    actions: { toggleExpand },
  } = grid.getInstance();

  const expandableDetails = renderExpandedDetails({ rowData });
  return (
    <ExpandableColumnCell
      description={renderRowDescription({ rowData })}
      expandableDetails={expandableDetails}
      extraData={renderExtraData({ rowData })}
      isExpanded={isExpanded}
      label={renderRowLabel({ rowData })}
      onToggleExpand={() => toggleExpand(rowData)}
    />
  );
};
const noop = () => null;
export default function createExpandableColumn({
  property,
  label,
  renderRowLabel = () => '----',
  renderRowDescription = noop,
  renderExtraData = noop,
  width,
  resizable,
  sortable,
  searchable,
  customRenderer,
  ...rest
}) {
  return {
    ...rest,
    label,
    property,
    cell: {
      formatters: [
        expandableColumnRenderer({
          renderRowLabel,
          renderRowDescription,
          renderExtraData,
        }),
      ],
    },
    width: width || '50%',
    expandableColumn: true,
    resizable,
    sortable,
    searchable,
  };
}
