import React from 'react'
import DSFilterBar from '@elliemae/ds-filterbar'

const addOptionalHeaderComponents = ({
  grid,
}) => {
  const {
    props: {
      showFilterBar,
      renderFilterToolbar,
    },
    state: {
      filters,
    },
    actions: {
      removeFilter,
      removeAllFilters,
    },
  } = grid.getInstance()

  return (
    <React.Fragment>
      {filters && !!filters.length && showFilterBar
      && (
      <DSFilterBar
        filters={filters}
        onRemoveAllFilters={removeAllFilters}
        onRemoveFilter={removeFilter}
        renderToolbar={renderFilterToolbar}
      />
      )}
    </React.Fragment>
  )
}

export default addOptionalHeaderComponents
