import React from 'react'
import PropTypes from 'prop-types'
import { PillGroup } from './PillGroup'
import { RemovablePill } from './RemovablePill'
import { Pill } from './Pill'

export function LabeledPills({
  children,
  label = '',
  leftAddon,
  onFocusGroupSet,
  onFocusNextGroup,
  onFocusPreviousGroup,
}) {
  return (
    <PillGroup
      onFocusGroupSet={onFocusGroupSet}
      onFocusNextGroup={onFocusNextGroup}
      onFocusPreviousGroup={onFocusPreviousGroup}
    >
      <Pill
        label={label}
        leftAddon={leftAddon}
        variant='title'
      />
      {children}
    </PillGroup>
  )
}

LabeledPills.propTypes = {
  label: PropTypes.string.isRequired,
  children: PropTypes.arrayOf(PropTypes.shape({
    type: PropTypes.oneOf([Pill, RemovablePill]),
  })).isRequired,
}
