/* eslint-disable react/display-name */
import React from 'react'
import FilterableHeader from './components/FilterableHeader'
import { getFilterLabels, getFilterLabelsGroup } from './helper'

function FilterableFormatter({
  grid,
  onAddFilter,
  query,
  filterMenuRef,
}) {
  return (value, { column }) => {
    const {
      rows,
      composedRows,
      props: {
        groupedRows,
        groupedBy,
      },
    } = grid.getInstance()

    const filteredLabels = groupedRows && !groupedBy
      ? getFilterLabelsGroup(column, rows, query)
      : getFilterLabels(column, composedRows.rows || composedRows, query)

    const columnData = column.filterOptions || filteredLabels
    return (
      <FilterableHeader
        column={column}
        columnData={columnData}
        filterMenuRef={filterMenuRef}
        onAddFilter={onAddFilter}
        value={value}
      />
    )
  }
}

export default FilterableFormatter
