import React, { useCallback, useState } from 'react'
import { DatePickerSingleDate } from '@elliemae/ds-date-picker'
import DSPopper, { PopperPositions as Position } from '@elliemae/ds-basic/Popper'
import DatePickerIcon from '@elliemae/ds-icons/DatePicker2'
import DSButton from '@elliemae/ds-basic/Button'
import moment from 'moment'

const SingleDatePickerContainer = ({
  onChange = () => null,
  showHeader,
}) => {
  const [date, setDate] = useState()

  const handleDateChange = useCallback((data) => {
    onChange(moment(data))
    setDate(data)
  }, [])

  return (
    <DatePickerSingleDate
      date={date}
      onChange={handleDateChange}
      showHeader={showHeader}
    />
  )
}

const formatDate = date => date.format('MM DD YYYY')

function SingleDateFilterMenu({
  column,
  className,
  isMenuOpened = false,
  onOpenMenu = () => null,
  placement = Position.BOTTOM_END,
  onAddFilter = () => null,
  dateFormatter = formatDate,
  showHeader = false,
  menuProps,
}) {
  return (
    <DSPopper
      contentComponent={(
        <div className={className}>
          <SingleDatePickerContainer
            minimumNights={0}
            onChange={(date) => {
              onAddFilter({
                group: column.property,
                label: column.label,
                value: dateFormatter(date),
                type: 'date',
                filterParams: { start: date, end: date },
                transformRowValue: value => moment(value)
              }, column)
              onOpenMenu(false)
            }
            }
            showHeader={showHeader}
          />
        </div>
      )}
      isOpen={isMenuOpened}
      onOpen={onOpenMenu}
      placement={placement}
      showArrow={false}
      triggerComponent={(
        <DSButton
          buttonType='text'
          icon={<DatePickerIcon />}
          onClick={() => (!isMenuOpened ? onOpenMenu(true) : onOpenMenu(false))}
          size='s'
        />
      )}
      zIndex={menuProps.zIndex}
    />
  )
}

export default SingleDateFilterMenu
