import React from 'react'
import onClickHandlerForNonInteractiveElements from '@elliemae/ds-utilities/onClickHandlerForNonInteractiveElements'
import { convertPropToCssClassName } from '@elliemae/ds-classnames'

const DSToggleRender = ({
  containerProps,
  id,
  className = '',
  style = {},
  hasError = false,
  readOnly = false,
  disabled = false,
  checked = false,
  onBlur = () => null,
  onFocus = () => null,
  onChange = () => null,
  onMouseDown = () => null,
  onMouseEnter = () => null,
  onMouseLeave = () => null,
  onTouchCancel = () => null,
  onTouchEnd = () => null,
  onTouchMove = () => null,
  onTouchStart = () => null,
  labelOn = 'ON',
  labelOff = 'OFF',
  dragStyle = {},
  containerRef = null,
  handRef = null,
  name = '',
  value = '',
  size = 's',
}) => {
  const { cssClassName, classNameBlock, classNameElement } = convertPropToCssClassName('form-element-toggle', className, {
    hasError,
    readOnly,
    disabled,
    checked,
    id,
    size,
  })
  const allowChange = disabled || readOnly
  return (
    <div
      {...containerProps}
      aria-disabled={disabled}
      defaultChecked={checked}
      defaultValue={value}
      {...onClickHandlerForNonInteractiveElements(allowChange ? null : onChange)}
      ref={containerRef}
      className={cssClassName}
      onBlur={onBlur}
      onFocus={onFocus}
    >
      <div className={classNameBlock('box')}>
        { checked && (
          <div className={classNameElement('icon-on')}>
            {labelOn}
          </div>
        )}
        { !checked && (
          <div className={classNameElement('icon-off')}>
            {labelOff}
          </div>
        )}
      </div>
      <div
        ref={handRef}
        aria-checked={checked}
        aria-disabled={disabled}
        className={classNameElement('input')}
        onMouseDown={allowChange ? null : onMouseDown}
        onMouseEnter={allowChange ? null : onMouseEnter}
        onMouseLeave={allowChange ? null : onMouseLeave}
        onTouchCancel={allowChange ? null : onTouchCancel}
        onTouchEnd={allowChange ? null : onTouchEnd}
        onTouchMove={allowChange ? null : onTouchMove}
        onTouchStart={allowChange ? null : onTouchStart}
        style={{
          ...style,
          ...dragStyle,
        }}
      />
      <input
        className={classNameElement('hidden-checkbox')}
        defaultChecked={checked}
        defaultValue={value}
        name={name}
        type='checkbox'
      />
    </div>
  )
}

export default DSToggleRender
