import React from 'react';
import DSButton from '@elliemae/ds-basic/Button';
import MoreOptionsVert from '@elliemae/ds-icons/MoreOptionsVert';
import { RowToolbarHoc } from './RowToolbarHoc';
import { RowSizesOptions } from '../../rowSizes';

export const ToolbarTrigger = (grid, rowData) =>
  RowToolbarHoc(
    // eslint-disable-next-line react/display-name
    React.forwardRef(
      ({ isScrolling, buttonMarginTop, hasScrollBar, ...rest }, ref) => {
        const {
          props: { rowSize },
        } = grid.getInstance();
        const scrollBarWidth = navigator.userAgent
          .toLowerCase()
          .includes('windows')
          ? 20
          : 10;
        return (
          <DSButton
            {...rest}
            buttonType="text"
            className="toolbar-trigger"
            data-testid="toolbar-trigger"
            icon={<MoreOptionsVert />}
            index={rowData.index}
            innerRef={ref}
            style={{
              position: 'absolute',
              marginLeft: 5,
              marginRight: hasScrollBar ? scrollBarWidth : 0,
              right: 0,
              order: 3,
              top: 'auto',
              marginTop: buttonMarginTop,
              zIndex: 2,
              transformStyle: 'preserve-3d',
              // ...(isScrolling && scrollingStyles),
              ...(rowSize === RowSizesOptions.normal
                ? {}
                : {
                    marginRight: 0,
                    marginTop: 0,
                  }),
            }}
            tabIndex={-1}
          />
        );
      },
    ),
    grid,
    rowData,
  );

export default ToolbarTrigger;
