import { get } from '@elliemae/ds-utilities/utils'
import createInstancePlugin from '@elliemae/ds-shared/createDataInstance/createInstancePlugin'

const isVisible = (column) => {
  if (typeof column === 'undefined') {
    return false
  }
  return typeof column.visible === 'undefined' || column.visible
}

export const getColumnValue = (row, property) => get(row, property)
export const getVisibleRowData = (row, columns) => columns.reduce((result, column) => {
  const columnProperty = column.property
  return isVisible(column)
    ? [
      ...result,
      getColumnValue(row, `_${columnProperty}`) || getColumnValue(row, columnProperty),
    ]
    : result
}, [])

const defaultOptions = uuid => ({
  sheetName: 'SheetName',
  fileName: `datagrid-${uuid}`,
  type: 'csv',
  onlySelection: false,
  excludedColumns: undefined,
})

const getOptions = (opts, uuid) => ({
  ...defaultOptions(uuid),
  ...opts,
})

export const getExportedData = (rows, columns, range) => {
  const rowsRanged = range ? rows.slice(...range) : rows
  const dataToExport = rowsRanged.map(row => getVisibleRowData(row, columns))
  const columnsContent = columns.reduce((acc, curr, idx) => {
    return `${acc}${idx === 0 ? '' : ','}${curr.label}`
  }, '')
  const rowsContent = dataToExport.map(e => e.join(',')).join('\r\n')
  const csvContent = `${columnsContent}\r\n${rowsContent}`
  return csvContent
}

// eslint-disable-next-line max-statements
const exportTo = (rows, columns, {
  range,
  fileName,
  type,
  excludedColumns,
}) => {
  if (excludedColumns) {
    columns = columns.filter(col => !excludedColumns.includes(col.property))
  }
  const csvContent = getExportedData(rows, columns, range)

  const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' })
  if (navigator.msSaveBlob) { // IE 10+
    navigator.msSaveBlob(blob, `${fileName}.${type}`)
  } else {
    const link = document.createElement('a')
    if (link.download !== undefined) { // feature detection
      // Browsers that support HTML5 download attribute
      const url = URL.createObjectURL(blob)
      link.setAttribute('href', url)
      link.setAttribute('download', `${fileName}.${type}`)
      link.style.visibility = 'hidden'
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
    }
  }
}

export const ExportDataPlugin = createInstancePlugin('export-data', {
  decorateGrid(instance) {
    return {
      exportRows(opts = {}) {
        const {
          composedRows: rows, columns, uuid, state: { selection },
        } = instance.getInstance()
        const options = getOptions(opts, uuid)
        // check paginated rows
        let currentRows = rows.allRows || rows
        if (opts.onlySelection) {
          const selectedRows = Object.keys(selection.selectedRows)
          currentRows = currentRows.filter(r => selectedRows.includes(r.id))
        }

        exportTo(currentRows, columns, options)
      },
      exportVisibleRows(opts = {}) {
        const {
          composedRows: rows, columns, uuid, state: { selection },
        } = instance.getInstance()
        const options = getOptions(opts, uuid)

        // check paginated rows
        let currentRows = rows.rows || rows
        if (opts.onlySelection) {
          const selectedRows = Object.keys(selection.selectedRows)
          currentRows = currentRows.filter(r => selectedRows.includes(r.id))
        }

        exportTo(currentRows, columns, options)
      },
    }
  },
})
