import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { aggregatedClasses } from '@elliemae/ds-classnames'
import CloseCircle from '@elliemae/ds-icons/CloseCircle'
import DSButton from '@elliemae/ds-basic/Button'
import DSTooltip from '@elliemae/ds-basic/Tooltip'
import InputAddonWrapper from './InputAddonWrapper'
import { showEllipsis } from './showEllipsis'

const blockName = 'input'
const Input = aggregatedClasses('input')(blockName, null, ({
  clearable,
  value,
}) => ({
  clearable,
  value,
}))
const WrapperForTooltipInput = aggregatedClasses('div')(blockName, 'tooltip-ref')
const InputCustomWrapper = aggregatedClasses('div')(blockName, null, ({
  clearable,
  value,
}) => ({
  clearable,
  value,
}))
const ClearableButton = aggregatedClasses(DSButton)(blockName, 'clearable')

const getValidComponents = component => component

const noop = () => null
class InputImpl extends Component {
  static defaultProps = {
    onBlur: noop,
    onFocus: noop,
  }
  constructor(props) {
    super(props)
    this.ref = React.createRef()
    this.refTooltip = React.createRef()
  }
  state = {
    width: -1,
    active: false,
  }
  componentDidMount() {
    this.setWidth()
    window.addEventListener('resize', this.setWidth)
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.setWidth)
  }
  handleBlur = (e) => {
    const { onBlur } = this.props
    onBlur(e)
    this.setState({
      active: false,
    })
  }
  handleFocus = (e) => {
    const { onFocus } = this.props
    onFocus(e)
    this.setState({
      active: true,
    })
  }
  setWidth = () => {
    const client = this.refTooltip.current.getBoundingClientRect()
    this.setState({
      width: client.width,
    })
  }
  handleClear() {
    const {
      onClear,
      onChange,
      clearable,
    } = this.props
    if (clearable && onClear) {
      onClear()
    } else {
      onChange({ target: { value: '' } })
    }

    if ((this.ref || {}).current) this.ref.current.focus()
  }
  renderClearableComponent() {
    const {
      clearable,
      value,
      disabled,
    } = this.props
    if (disabled) return null
    return clearable && value ? (
      <ClearableButton
        buttonType='text'
        data-testid='ds-input_clearable-btn'
        disabled={disabled}
        icon={<CloseCircle />}
        onClick={this.handleClear}
      />
    ) : null
  }
  render() {
    const {
      role,
      id,
      isShowElipsisActive,
      autoFocus,
      className,
      name,
      innerRef,
      maxLength,
      minLength,
      placeholder,
      leftComponent,
      rightComponent,
      disableTooltip,
      readOnly,
      disabled,
      value,
      onClick,
      onChange,
      onKeyDown,
      onKeyUp,
      onFocus,
      onBlur,
      onPaste,
      required,
      type,
      customInputType,
      min,
      max,
      clearable,
      onMouseLeave,
      onMouseOut,
      style,
      containerProps,
      tabIndex,
      ...rest
    } = this.props
    const { width, active } = this.state
    const rightComponents = [this.renderClearableComponent(), rightComponent].filter(getValidComponents)
    const leftComponents = [leftComponent].filter(getValidComponents)

    const inputProps = {
      'aria-label': rest['aria-label'],
      'aria-placeholder': placeholder,
      'aria-required': required,
      'aria-multiline': false,
      'aria-autocomplete': 'none',
      'data-testid': rest['data-testid'],
      role,
      id,
      ref: this.ref,
      className,
      autoFocus,
      disabled: disabled || readOnly,
      innerRef,
      maxLength,
      minLength,
      name,
      onBlur,
      onChange,
      onClick,
      onFocus,
      onKeyDown,
      onKeyUp,
      onPaste,
      onMouseLeave,
      disableTooltip,
      onMouseOut,
      placeholder,
      required,
      type,
      value,
      min,
      max,
      style,
      tabIndex,
      ...containerProps,
    }

    let InputComponent = (
      <WrapperForTooltipInput ref={this.refTooltip} style={{ width: '100%' }}>
        {!customInputType
          ? (
            <Input
              {...inputProps}
              clearable={clearable}
              onBlur={this.handleBlur}
              onFocus={this.handleFocus}
              value={value}
            />
          )
          : (
            <InputCustomWrapper {...containerProps} clearable={clearable} value={value}>
              {customInputType({
                ...inputProps,
              })}
            </InputCustomWrapper>
          )}
      </WrapperForTooltipInput>
    )
    if (showEllipsis(width, value) && !active && width && isShowElipsisActive) {
      InputComponent = (
        <DSTooltip
          isOpen={disableTooltip}
          title={String(value)}
          triggerComponent={(
            <WrapperForTooltipInput style={{ width: '100%' }}>
              {InputComponent}
            </WrapperForTooltipInput>
          )}
        />
      )
    }


    const renderInput = rightComponents.length || leftComponents.length || clearable
      ? (
        <InputAddonWrapper
          className={className}
          leftComponents={leftComponents}
          rightComponents={rightComponents}
          {...containerProps}
        >
          {InputComponent}
        </InputAddonWrapper>
      )
      : InputComponent

    return renderInput
  }
}

InputImpl.propTypes = {
  className: PropTypes.string,
  customInputType: PropTypes.func,
  id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  isShowElipsisActive: PropTypes.bool,
  max: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  min: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  name: PropTypes.string,
  onBlur: PropTypes.func,
  onChange: PropTypes.func,
  onClick: PropTypes.func,
  onFocus: PropTypes.func,
  onKeyDown: PropTypes.func,
  onKeyUp: PropTypes.func,
  onMouseLeave: PropTypes.func,
  onMouseOut: PropTypes.func,
  onPaste: PropTypes.func,
  placeholder: PropTypes.string,
  required: PropTypes.bool,
  role: PropTypes.string,
  // eslint-disable-next-line react/forbid-prop-types
  style: PropTypes.object,
  tabIndex: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  /**
   * Should be of type string if using ellipsis tooltip [isShowElipsisActive] prop
   */
  value(props, propName, componentName) { // eslint-disable-line consistent-return
    if (!!props.isShowElipsisActive && (typeof props[propName] !== 'string')) {
      return new Error(
        `Invalid prop \`${propName}\` of type \`${typeof props[propName]}\` supplied to`
        + ` \`${componentName}\`. Prop \`value\` should be of type \`string\` if prop \`isShowElipsisActive\` is set to \`true\`.`
        + ' Either set `isShowElipsisActive` to `false`, or properly format your `value` prop to `string`.',
      )
    }
  },
}

export default InputImpl
