import React from 'react'
import cx from 'obj-str'
import convertPropToCssClassName from './classNameManager'
import DimsumConfig from '../../../dimsum.config'

export const cssPrefix = DimsumConfig.classNamePrefix

const generateClassNameWithModifier = (block, element, modifier) => `${cssPrefix}-${block}${element
  ? `__${element}` : ''}--${modifier}`

const appendBlockAndPrefixToObj = (obj, block, element) => (obj ? Object.keys(obj)
  .reduce((acc, key) => ({ ...acc, [`${generateClassNameWithModifier(block, element, key)}`]: obj[key] }), {}) : [])

function aggregatedClasses(type, props) {
  // aggregatedClasses options parameters
  return (block,
    element,
    additionalModifiers,
    options = {},
  ) => {
    return React.forwardRef(({
      innerRef,
      children,
      classProps,
      useComponent,
      className,
      as,
      ...rest
    }, ref) => {
      // get the additional classNames either from a function or an object
      const otherModifiers = typeof additionalModifiers === 'function'
        ? appendBlockAndPrefixToObj(additionalModifiers(
          { ...rest, ...classProps, className }), block, element,
        )
        : appendBlockAndPrefixToObj(additionalModifiers, block, element)
      const otherModifiersStr = cx(otherModifiers)
      const { prefix } = options
      const { cssClassName: classNames } = convertPropToCssClassName(
        block,
        null,
        { ...classProps },
        { prefix, element },
      )

      return React.createElement(as || type, {
        ref: innerRef || ref,
        className: [classNames, otherModifiersStr, className].join(' '),
        ...props,
        ...rest,
      }, children)
    })
  }
}

export default aggregatedClasses
