/* eslint-disable react/no-unknown-property*/
import React, { useRef } from 'react'
import PropTypes from 'prop-types'
import { convertPropToCssClassName } from '@elliemae/ds-classnames'

const DSRadio = ({
  containerProps = {},
  className = '',
  style,
  labelText,
  htmlFor,
  hasError = false,
  readOnly = false,
  disabled = false,
  checked = false,
  value,
  name = '',
  onChange = () => null,
  children = null,
  tabIndex = 0,
  ...otherProps
}) => {
  const { cssClassName, classNameElement } = convertPropToCssClassName(
    'form-element-radio',
    className, {
      hasError,
      readOnly,
      disabled,
    })
  const checkRef = useRef(null)
  return (
    <div {...containerProps} className={cssClassName}>
      <label className={classNameElement('label')} htmlFor={htmlFor}>
        <input
          {...otherProps}
          ref={checkRef}
          aria-disabled={disabled}
          checked={checked}
          className={classNameElement('input')}
          disabled={disabled}
          name={name}
          onChange={(e) => {
            if (!readOnly && !disabled) {
              onChange(e)
            }
          }}
          readOnly={readOnly}
          tabIndex={-1}
          type='radio'
          value={value}
        />
        <span
          ref={otherProps.innerRef}
          aria-checked={checked}
          aria-disabled={disabled}
          className={classNameElement('input-button')}
          onKeyDown={(e) => {
            e.target = checkRef.current
            if (disabled) return
            if (e.key === 'Enter' || e.keyCode === 32) {
              onChange(e)
            } else if (otherProps.onKeyDown) {
              otherProps.onKeyDown(e)
            }
          }}
          role='radio'
          style={style}
          tabIndex={tabIndex}
          value={value}
        >
          <svg height='100%' width='100%'>
            <circle className={checked ? 'checked' : 'unchecked'} cx='50%' cy='50%' r='4' />
          </svg>
        </span>
        <span className={classNameElement('label-text')}>
          {labelText}
        </span>
      </label>
      {children && (
      <div className={classNameElement('radio-children')}>
        {children}
      </div>
      )}
    </div>
  )
}

DSRadio.propTypes = {
  containerProps: PropTypes.shape({}),
  /**
   * Displayable label for the radio
   */
  labelText: PropTypes.string,
  htmlFor: PropTypes.element,
  /**
   * Whether the radio has error or not
   */
  hasError: PropTypes.bool,
  /**
   * Whether the radio is read only or not
   */
  readOnly: PropTypes.bool,
  /**
   * Whether the radio is disabled or not
   */
  disabled: PropTypes.bool,
  /**
   * Whether the radio is checked or not
   */
  checked: PropTypes.bool,
  /**
   * Value that takes the checkbox if it is checked
   */
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.object,
  ]).isRequired,
  /**
   * Allows a function that is triggered once the radio changes
   */
  onChange: PropTypes.func,
  /**
   * Optional, if you want a checkbox customized
   */
  children: PropTypes.node,
}

export default DSRadio
