/* eslint-disable react/display-name */
import React from 'react'
import { cx } from '@elliemae/ds-utilities/utils'
import DSButton from '@elliemae/ds-basic/Button'
import { aggregatedClasses } from '@elliemae/ds-classnames'

const Intents = {
  SUCCESS: 'success',
  INFO: 'info',
  DANGER: 'danger',
  WARNING: 'warning',
}

const IntentIconColors = {
  [Intents.SUCCESS]: ['success', 900],
  [Intents.INFO]: ['brand-primary', 600],
  [Intents.DANGER]: ['danger', 900],
  [Intents.WARNING]: ['warning', 500],
}

const ValueWrapper = aggregatedClasses('span')('icon-column-cell')

const addColorToIcon = (icon, intentColor) => {
  if (icon.props.color) {
    return icon
  }
  return React.cloneElement(icon, { color: intentColor })
}

export default function createColumn({
  property,
  label,
  headerIcon,
  getRowIcon = () => null,
  isVisited = () => null,
  intent = Intents.SUCCESS,
  width = 60,
  onClick,
  visible,
  resizable,
  sortable,
  searchable,
}) {
  const formatter = (value, extraParams) => {
    const icon = getRowIcon(value, extraParams)
    const onClickHandler = e => onClick(e, extraParams)
    return (
      <ValueWrapper className={cx(`icc-${intent}`, isVisited(value, extraParams))}>
        {icon && addColorToIcon(icon, IntentIconColors[intent])}
        {onClick && typeof value !== 'undefined'
          ? (
            <DSButton
              buttonType='text'
              intent={intent}
              labelText={value}
              onClick={onClickHandler}
            />
          )
          : value
        }
      </ValueWrapper>
    )
  }
  return {
    property,
    label,
    headerStyle: {
      justifyContent: 'center',
    },
    header: {
      formatters: [() => (
        <span>
          {React.cloneElement(headerIcon, {
            color: IntentIconColors[intent],
          })}
        </span>
      )],
    },
    cell: {
      formatters: [formatter],
    },
    cellStyle: {
      justifyContent: 'center',
    },
    width,
    visible: typeof visible === 'undefined' || visible,
    resizable,
    sortable,
    searchable,
  }
}
