import React, { useMemo } from 'react'
import createInstancePlugin from '@elliemae/ds-shared/createDataInstance/createInstancePlugin'
import { appendCellFormatter } from '@elliemae/ds-shared/useDataGrid/initColumnDefinition'
import ToolbarProvider from '@elliemae/ds-shared/toolbar/ToolbarProvider'
import { RowRenderer } from './RowRenderer'

// eslint-disable-next-line react/display-name
const ToolbarHoc = (Component, extras) => props => (
  <ToolbarProvider {...extras}>
    <Component {...props} />
  </ToolbarProvider>
)

const decorateColumns = (columns) => {
  const lastColumn = columns[columns.length - 1]
  return [
    ...columns.slice(0, -1),
    appendCellFormatter([
      value => (
        <>
          {value}
          <div style={{ width: 40 }} />
        </>
      )], lastColumn, 'fixedFormatters'),
  ]
}

const decorateRenderers = (renderers, grid) => {
  const TableComponent = renderers.table
  const Table = useMemo(() => ToolbarHoc(TableComponent, { grid }), [])
  const Row = renderers.body.row

  renderers.body.row = RowRenderer(Row, grid)

  renderers.table = Table
  return renderers
}

export const ToolbarPlugin = createInstancePlugin('toolbar', {
  decorateRenderers,
  decorateColumns,
})
