/* eslint-disable max-lines */
import React from 'react'
import moment from 'moment'
import { convertPropToCssClassName } from '@elliemae/ds-classnames'
import DSInputMask, { MASK_TYPES, MASK_PIPES } from '@elliemae/ds-basic/form/InputMask'
import DSFormItemLayout from '@elliemae/ds-basic/form/FormItem'
import DSPopper from '@elliemae/ds-basic/Popper'

import { DatePickerPicker } from '@elliemae/ds-date-picker'
import DayPickerRangeController from './DayPickerRangeController'

export const START_DATE = 'startDate'
export const END_DATE = 'endDate'

const { classNameElement, classNameBlock } = convertPropToCssClassName('datepicker')

/**
 * DSDateRangePicker
 * @param {*} {}
 */
export default class DSDateRangePickerImpl extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      startDate: props.startDate ? moment(props.startDate) : moment(),
      endDate: props.endDate ? moment(props.endDate) : moment(),
      focusedInput: START_DATE,
      showPicker: false,
    }
  }
  componentDidMount() {
    document.addEventListener('keydown', this.handleKeyDown, false)
  }
  componentWillUnmount() {
    document.removeEventListener('keydown', this.handleKeyDown, false)
  }
  onDatesChange = ({ startDate, endDate }) => {
    this.setState({
      startDate,
      endDate,
      primaryStartInput: false,
      primaryEndInput: false,
    })
  }
  onFocusChange = () => {
    this.setState(({ focusedInput }) => {
      return {
        focusedInput: focusedInput === START_DATE ? END_DATE : START_DATE,
      }
    })
  }
  handleKeyDown = (event) => {
    if (event.key === 'Escape') this.setState({ showPicker: false })
  }
  getSafeStartInputValue = () => {
    const { startInput, primaryStartInput, startDate } = this.state
    if (primaryStartInput && startInput) return startInput.target.value    
    if (!startDate) return ''
    return startDate.format('MMDDYYYY')
  }
  getSafeEndInputValue = () => {
    const { endInput, primaryEndInput, endDate } = this.state
    if (primaryEndInput && endInput) return endInput.target.value
    if (!endDate) return ''
    return endDate.format('MMDDYYYY')
  }
  changeFocus = () => {
    const { disabled } = this.props
    if (!disabled) {
      this.setState(() => ({ focusedInput: 'startDate' }))
    }
  }
  handleChangeStart = (dataInput) => {
    const momentvalue = dataInput.target.value ? moment(dataInput.target.value) : moment()
    this.setState({ startDate: momentvalue || moment(), startInput: dataInput, primaryStartInput: true })
  }
  handleChangeEnd = (dataInput) => {
    const momentvalue = dataInput.target.value ? moment(dataInput.target.value) : moment().add(10, 'days')
    this.setState({ endDate: momentvalue, endInput: dataInput, primaryEndInput: true })
  }
  handleChangeDate = ({ startDate: _start, endDate }) => {
    if (_start && endDate) {
      this.setState({
        startDate: moment(_start),
        endDate,
        primaryStartInput: false,
        primaryEndInput: false,
      })
    } else if (_start) {
      this.setState({ startDate: moment(_start), primaryStartInput: false })
    } else if (endDate) {
      this.setState({ endDate, primaryEndInput: false })
    }
  }
  render() {
    const {
      startDate,
      endDate,
      focusedInput,
      showPicker,
    } = this.state

    const {
      disabled,
      displayFormatDay,
      minimumNights,
      labelFrom,
      labelTo,
    } = this.props

    const inputClass = classNameBlock('inputs', 'range-picker')
    const inputClassModifier = ''

    const safeStartInputValue = this.getSafeStartInputValue()
    const safeEndInputValue = this.getSafeEndInputValue()
    return (
      <div
        className={`${inputClass} ${inputClassModifier}`}
      >
        <div className={classNameBlock('wrapper-input-date-range-picker-controller')}>
          <DSFormItemLayout
            floatingLabel
            inputComponent={DSInputMask}
            labelText={labelFrom}
            mask={MASK_TYPES.DATE}
            onChange={this.handleChangeStart}
            onFocus={() => this.setState({ showPicker: false })}
            pipe={MASK_PIPES.AUTO_CORRECT_DATE}
            value={safeStartInputValue}
          />
          <DSFormItemLayout
            floatingLabel
            inputComponent={DSInputMask}
            labelText={labelTo}
            mask={MASK_TYPES.DATE}
            onChange={this.handleChangeEnd}
            onFocus={() => this.setState({ showPicker: false })}
            pipe={MASK_PIPES.AUTO_CORRECT_DATE}
            value={safeEndInputValue}
          />
        </div>
        <div className={classNameBlock('wrapper-button-date-range-picker-controller')}>
          <DSPopper
            contentComponent={(
              <DayPickerRangeController
                displayFormatDay={displayFormatDay}
                endDate={endDate}
                focusedInput={focusedInput}
                hideKeyboardShortcutsPanel
                minimumNights={minimumNights}
                onDatesChange={this.onDatesChange}
                onFocusChange={this.onFocusChange}
                onOutsideClick={() => this.setState({ showPicker: false })}
                startDate={startDate}
              />
            )}
            isOpen={showPicker}
            placement='bottom-end'
            showArrow={false}
            triggerComponent={(
              <div onFocus={() => this.setState({ showPicker: true })}>
                <DatePickerPicker
                  className={classNameElement('picker')}
                  disabled={disabled}
                  onClick={() => this.setState({ showPicker: true })}
                />
              </div>
            )}
          />
        </div>
      </div>
    )
  }
}
