/* eslint-disable no-console */
import React, { useState, useRef, useEffect } from 'react'
import DSDateInput from '@elliemae/ds-basic/form/DateInput'
import DSInputGroup from '@elliemae/ds-basic/dsinputgroup'
import { aggregatedClasses } from '@elliemae/ds-classnames'

const blockname = 'date-picker-header'
const Header = aggregatedClasses('div')(blockname)

const DatePickerHeader = ({
  onInputChange = () => null,
  selectedDate,
  updateKeyDate,
}) => {
  const [value, setValue] = useState(selectedDate)
  const dateInputRef = useRef()

  useEffect(() => {
    setValue(selectedDate)
  }, [selectedDate])

  const handleOnChange = (newDate) => {
    dateInputRef.current.firstChild.getElementsByClassName('input-year')[0].focus()
    setValue(newDate)
  }

  const handleOnKeyDown = (e) => {
    if (e.key === 'Enter') {
      onInputChange(value)
      updateKeyDate(value)
    }
  }

  return (
    <Header>
      <DSInputGroup>
        <DSDateInput
          clearable={false}
          innerRef={dateInputRef}
          onChange={handleOnChange}
          onKeyDown={handleOnKeyDown}
          value={value}
        />
      </DSInputGroup>
    </Header>
  )
}

export default DatePickerHeader
